import {
  getCommunitiesList,
  showCommunity,
  setCoverRegionCommunity,
  getRegionList,
  verifyUpdateCommunity,
  verifyCommunity,
  getApplyByCommunity,
  updateCommunity,
  inviteCode,
  setSelfCommunity,
  ban
} from './service'
import store from '@/store'

export default {
  namespaced: true,
  state: {
    res: {},
    allregion: [],
    searchregion: [],
    _fliterregion: "",
  },
  mutations: {
    res(state, para) {
      state.res = para
    },
    allregion(state, para) {
      state.allregion = para
    },
    searchregion(state, para) {
      state.searchregion = para
    },
    _fliterregion(state, para) {
      state._fliterregion = para
    },
  },
  actions: {
    async getCommunitiesList({
      commit,
      state
    }, payload) {
      let {
        data
      } = await getCommunitiesList(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        let arr = await data.result.data.map(x => {
          // 
          if (x.long_create_time === 0) {
            x.long_create_time = "-";
          } else {
            x.long_create_time = $async.createTime(
              x.long_create_time,
              "yy-mm-dd hh:MM"
            );
          }
          if (
            $async
            .createTime(x.long_apply_time, "yy-mm-dd hh:MM")
            .indexOf("1970") > -1
          ) {
            x.long_apply_time = "-";
          } else {
            x.long_apply_time = $async.createTime(
              x.long_apply_time,
              "yy-mm-dd hh:MM"
            );
          }
          if (
            $async
            .createTime(x.long_verify_time, "yy-mm-dd hh:MM")
            .indexOf("1970") > -1
          ) {
            x.long_verify_time = "-";
          } else {
            x.long_verify_time = $async.createTime(
              x.long_verify_time,
              "yy-mm-dd hh:MM"
            );
          }
          // 
          if (x.active === 0) {
            x.active = "";
          } else {
            x.active = "";
          }
          // 
          if (x.invite_code === "0") {
            x.invite_code = "-";
            x.invite_type = "";
          } else {
            x.invite_type = "";
          }
          // shifou
          if (x.int_self === 0) {
            x.int_self_2 = "";
          } else {
            x.int_self_2 = "";
          }
          // 
          if (x.int_apply_type === 0) {
            x.int_apply_type = "";
          } else if (x.int_apply_type === 1) {
            x.int_apply_type = "";
          } else {
            x.int_apply_type = "";
          }
          return x;
        });

        let result = {
          data: arr,
          // 
          needBindCover: data.result.needBindCover,
          count: data.result.count
        }
        commit('res', result)
      } else {
        $message.error(data.result)
      }
    },
    async getRegionList({
      commit,
      state
    }, payload) {
      let {
        data
      } = await getRegionList(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        let arr1 = await data.result.data.map(x => {
          return {
            value: x.region_id,
            label: x.region_name
          };
        });
        let arr2 = [],
          _fliterregion;
        if (store.state.authUser.type === 1) {
          arr2 = arr1;
          _fliterregion = "0";
        } else {
          columnCity.forEach((x, i) => {
            arr1.forEach((y, j) => {
              if (y.value === columnCity[i]) {
                arr2.push({
                  value: y.value,
                  label: y.label
                });
              }
            });
          });
          _fliterregion = arr2[0].value;
        }
        commit('allregion', arr1);
        commit('searchregion', arr2);
        commit('_fliterregion', _fliterregion);
      }
    },
    async showCommunity({
      commit
    }, payload) {
      let {
        data
      } = await showCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        return data.result
      } else {
        $message.error(data.result);
      }
    },
    async ban({
      commit
    }, payload) {
      let {
        data
      } = await ban(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        return true
      } else {
        $message.error(data.result);
      }
    },
    async setCoverRegionCommunity({
      commit
    }, payload) {
      let {
        data
      } = await setCoverRegionCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message({
          type: "success",
          message: ""
        })
        return true
      } else {
        $message.error(data.result);
      }
    },
    async fnVerifyCommunity({
      commit,
      dispatch
    }, payload) {
      if (payload.flag === 0) {
        if (payload.text === "") {
          $message.warning("");
          return false;
        }
      } else {
        payload.text === "";
      }
      if (payload.filtercondition_type == "") {
        delete payload['filtercondition_type']
        dispatch('verifyCommunity', payload)
      } else if (payload.filtercondition_type == "") {
        delete payload['filtercondition_type']
        dispatch('verifyUpdateCommunity', payload)
      } else {
        return false;
      }
    },
    async verifyUpdateCommunity({
      commit
    }, payload) {
      let {
        data
      } = await verifyUpdateCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message({
          type: "success",
          message: "!"
        })
        return true
      } else {
        $message.error(data.result);
      }
    },
    async verifyCommunity({
      commit
    }, payload) {
      let {
        data
      } = await verifyCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message({
          type: "success",
          message: "!"
        })
        return true
      } else {
        $message.error(data.result);
      }
    },
    async getApplyByCommunity({
      commit
    }, payload) {
      let {
        data
      } = await getApplyByCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        return data.result
      } else {
        $message.error(data.result);
      }
    },
    async inviteCode({
      commit
    }, payload) {
      let {
        data
      } = await inviteCode(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message.success("!");
        return true
      } else {
        $message.error(data.result);
      }
    },
    async setSelfCommunity({
      commit
    }, payload) {
      let {
        data
      } = await setSelfCommunity(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        return true
      } else {
        $message.error(data.result);
      }
    },
  },
};