import {
  getUserList,
  getMenuList,
  getRegionList,
  searchUser,
  delUserInfo,
  updateUserInfo
} from './service'

export default {
  namespaced: true,
  state: {
    userList: {},
    getRegionList: {},
    searchuserinfo: {},
    permissionlist: {},
    regionlist: {}
  },
  mutations: {
    userList(state, para) {
      state.userList = para
    },
    searchuserinfo(state, para) {
      state.searchuserinfo = para
    },
    permissionlist(state, para) {
      state.permissionlist = para
    },
    regionlist(state, para) {
      state.regionlist = para
    }
  },
  actions: {
    // 
    async getUserList({
      commit,
      state
    }, payload) {
      let {
        data
      } = await getUserList(payload).catch(err => {
        $message.error('')
      })
      let arr = await data.result.data.map(x => {
        x.createTime = $async.createTime(x.createTime, "yy-mm-dd hh:MM");
        x.newUser = x.newUser === 1 ? "" : "-";
        return x;
      });
      commit('userList', data.result)
    },
    // 
    async updateUserInfo({
      commit,
      state
    }, payload) {
      let {
        data
      } = await updateUserInfo(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message.success("");
        return true
      } else {
        $message.error(data.result);
      }
    },
    // 
    async delUserInfo({
      commit,
    }, payload) {
      let {
        data
      } = await delUserInfo(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        $message.success("");
        return true
      } else {
        $message.error(data.result);
      }
    },
    // 
    async searchUser({
      commit
    }, payload) {
      let {
        data
      } = await searchUser(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        commit('searchuserinfo', data.result)
        $message.success("");
        return true
      } else {
        $message.error(data.result);
      }
    },
    // 
    async getMenuList({
      commit,
      dispatch,
      state
    }, payload) {
      let {
        data
      } = await getMenuList().catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        dispatch('getRegionList', payload)
        commit('permissionlist', data.result)
      } else {
        $message.error(data.result);
      }
    },
    // 
    async getRegionList({
      commit,
      state
    }, payload) {
      let {
        data
      } = await getRegionList(payload).catch(err => {
        $message.error('')
      })
      if (data.code === 0) {
        commit('regionlist', data.result.data)
        return true
      }
    }
  },
};